<template>
  <div class="star-rating-wrap">
    <div v-for="(rating, index) in ratings" :key="index" @click="setSelectedRating(index + 1)">
      <font-awesome-icon :icon="starSolid" class="icon star-solid" v-if="rating" />
      <font-awesome-icon :icon="starRegular" class="icon star-regular" v-else />
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, SetupContext, watch, ref } from 'vue'
import { faStar as faStarSolid } from '@fortawesome/free-solid-svg-icons'
import { faStar as faStarRegular } from '@fortawesome/free-regular-svg-icons'

interface Props {
  selectedRating: number
}
export default defineComponent({
  props: {
    selectedRating: {
      type: Number,
      required: true
    }
  },
  emits: ['set-score'],
  setup(props: Props, context: SetupContext) {
    const starSolid = computed(() => faStarSolid)
    const starRegular = computed(() => faStarRegular)

    const ratings = ref([false, false, false, false, false])

    const setSelectedRating = (rating: number) => {
      context.emit('set-score', rating)
    }

    const updateRatings = (rating: number) => {
      ratings.value = ratings.value.map(() => false)
      for (let i = 0; i < rating; i++) {
        ratings.value[i] = true
      }
    }
    updateRatings(props.selectedRating)

    watch(
      () => props.selectedRating,
      (selectedRating) => {
        updateRatings(selectedRating)
      }
    )
    return {
      starSolid,
      starRegular,
      ratings,
      setSelectedRating
    }
  }
})
</script>

<style scoped lang="scss">
.star-rating-wrap {
  display: flex;
  justify-content: center;

  .icon {
    font-size: 2rem;
    cursor: pointer;

    &:hover {
      color: #fabb04;
    }
  }

  .star-solid {
    color: #fabb04;
  }

  .star-regular {
    color: #606060;
  }
}
</style>
