import { gql } from '@apollo/client/core'

const updateReviewMutation = gql`
  mutation UpdateReview($id: Int, $score: Int, $comment: String) {
    update_Reviews(where: { _and: { id: { _eq: $id } } }, _set: { score: $score, comment: $comment }) {
      returning {
        id
      }
    }
  }
`

export default updateReviewMutation
