import { gql } from '@apollo/client/core'

const reviewForm = gql`
  query ReviewForm($id: uuid, $student_id: uuid) {
    SubscriptionCourses(where: { id: { _eq: $id } }) {
      course_id
      Course {
        image
        title
        Instructor {
          icon
          name
          id
        }
        Reviews(where: { student_id: { _eq: $student_id } }) {
          id
          score
          comment
        }
      }
    }
  }
`

export default reviewForm
