
import { defineComponent, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { useQuery, useResult, useMutation } from '@vue/apollo-composable'
import { useStore } from 'vuex'

import reviewForm from '@/graphql/queries/reviewForm'
import insertReviewMutation from '@/graphql/mutations/insertReview'
import updateReviewMutation from '@/graphql/mutations/updateReview'
import { ReviewFormQuery, InsertReviewMutation, UpdateReviewMutation } from '@/graphql/@types/types'

import StarRatingButton from '@/components/StarRatingButton.vue'
import Header from '@/components/Header.vue'
import NoticeComponent from '@/components/Notice.vue'

export default defineComponent({
  components: {
    StarRatingButton,
    Header,
    NoticeComponent
  },
  setup() {
    const { currentRoute } = useRouter()
    const subscriptionCourseId = currentRoute.value.query.course_id
    const store = useStore()
    const router = useRouter()

    const state = reactive({
      score: 3,
      comment: '' as string | undefined | null,
      notice: false,
      reviewId: null as null | number,
      noticeText: ''
    })

    const { result, onError, onResult } = useQuery<ReviewFormQuery>(reviewForm, {
      id: subscriptionCourseId,
      student_id: store.state.currentUser?.uid
    })
    onError(() => {
      // TODO Notfoundページにリダイレクト resultがundefinedのときもリダイレクトさせる
      router.push({ name: 'Home' })
    })
    const courseId = useResult(result, null, (data) => data.SubscriptionCourses[0].course_id)
    const course = useResult(result, null, (data) => data.SubscriptionCourses[0].Course)
    const instructor = useResult(result, null, (data) => data.SubscriptionCourses[0].Course.Instructor)

    onResult((queryResult) => {
      const review = queryResult.data.SubscriptionCourses[0].Course.Reviews
      if (!review.length) return
      state.score = review[0].score
      state.comment = review[0].comment
      state.reviewId = review[0].id
    })

    const { mutate: insertReview, onDone: insertReviewDone } = useMutation<InsertReviewMutation>(insertReviewMutation, () => ({
      variables: {
        course_id: courseId.value,
        student_id: store.state.currentUser?.uid,
        score: state.score,
        // title: state.title,
        comment: state.comment
      }
    }))

    const { mutate: updateReview, onDone: updateReviewDone } = useMutation<UpdateReviewMutation>(updateReviewMutation, () => ({
      variables: {
        id: state.reviewId,
        score: state.score,
        comment: state.comment
      }
    }))

    const noticeActive = (noticeText: string) => {
      state.notice = true
      state.noticeText = noticeText
      setTimeout(() => (state.notice = false), 1500)
    }

    // TODO レビュー送信後のリダイレクト処理
    insertReviewDone(() => {
      noticeActive('レビューが送信されました')
    })

    updateReviewDone(() => {
      noticeActive('レビューが編集されました')
    })

    const setScore = (payload: number) => {
      state.score = payload
    }

    const submit = () => {
      if (state.reviewId) {
        updateReview()
      } else {
        insertReview()
      }
    }

    return {
      state,
      course,
      instructor,
      setScore,
      submit
    }
  }
})
