import { gql } from '@apollo/client/core'

const insertReviewMutation = gql`
  mutation InsertReview($course_id: Int, $student_id: uuid, $score: Int, $comment: String) {
    insert_Reviews(objects: { course_id: $course_id, student_id: $student_id, score: $score, comment: $comment }) {
      returning {
        id
      }
    }
  }
`

export default insertReviewMutation
